@-webkit-keyframes hover{from{width:95%;height:95%}to{width:100%;height:100%}}@keyframes hover{from{width:95%;height:95%}to{width:100%;height:100%}}.log-page{display:-ms-flexbox;display:flex;-ms-flex-pack:distribute;justify-content:space-around;-ms-flex-align:center;align-items:center;-ms-flex-direction:column;flex-direction:column;position:relative;background:#317c34;width:80%;height:80%;padding:2rem 1.5rem;color:#fefefe;font:2.2rem/1 "SFUIText","sans-serif";border:solid 0.1rem #874206;border-radius:18px}@media (min-width: 768px){.log-page{max-width:75%;padding:2.5rem 5rem}}@media (min-width: 900px){.log-page{max-width:70%;padding:3rem 6rem}}@media (min-width: 1200px){.log-page{max-width:80rem;padding:3.5rem 7rem}}.log-page h2{margin:0 auto;font-size:3rem;text-align:center;width:80%}.log-page h2.error{color:#ffd600}@media (min-width: 768px){.log-page h2{font-size:3.5rem;line-height:7rem}}.rr{width:100vh}.rr-enter{opacity:0.01;-webkit-transform:translateX(-100%);transform:translateX(-100%)}.rr-enter-active{opacity:1;-webkit-transform:translateX(0%);transform:translateX(0%);-webkit-transition:all 300ms ease-in;transition:all 300ms ease-in}.rr-exit{opacity:1;-webkit-transform:translateX(0%);transform:translateX(0%)}.rr-exit-active{opacity:0.01;-webkit-transform:translateX(100%);transform:translateX(100%);-webkit-transition:all 300ms ease-out;transition:all 300ms ease-out}
