.game-timer{-webkit-box-shadow:inset 0 0 1.5rem rgba(10,10,10,0.7);box-shadow:inset 0 0 1.5rem rgba(10,10,10,0.7)}@-webkit-keyframes hover{from{width:95%;height:95%}to{width:100%;height:100%}}@keyframes hover{from{width:95%;height:95%}to{width:100%;height:100%}}.game-timer{position:absolute;top:-0.5rem;left:50%;-webkit-transform:translateX(-50%);transform:translateX(-50%);background:radial-gradient(ellipse farthest-side, #9f4e07, #9a4c07);padding:0.5rem;text-align:center;border-radius:3rem;width:15rem;color:#eaf9fe;z-index:2}
