.burger-menu-button:hover,.burger-menu-button:focus{-webkit-box-shadow:inset 0 0 1.5rem rgba(10,10,10,0.7);box-shadow:inset 0 0 1.5rem rgba(10,10,10,0.7)}.burger-menu-button{-webkit-box-shadow:inset 0 0 2.5rem rgba(10,10,10,0.5);box-shadow:inset 0 0 2.5rem rgba(10,10,10,0.5)}@-webkit-keyframes hover{from{width:95%;height:95%}to{width:100%;height:100%}}@keyframes hover{from{width:95%;height:95%}to{width:100%;height:100%}}.burger-menu-button{position:absolute;top:0.7rem;right:0;display:-ms-flexbox;display:flex;-ms-flex-pack:center;justify-content:center;-ms-flex-align:center;align-items:center;-ms-flex-direction:column;flex-direction:column;height:4.1rem;width:4.1rem;background:transparent;border:solid 1px #6e220b;outline:none;border-radius:4.1rem;cursor:pointer}.burger-menu-button span{position:relative;display:block;width:2rem;height:.3rem;margin:2px;background:#6e220b;-webkit-transform:rotate(0);transform:rotate(0);-webkit-transition:all 0.5s;transition:all 0.5s;border-radius:1rem}.burger-menu-button .middle-line{opacity:1;-webkit-transition:all 0.5s;transition:all 0.5s}@media (min-width: 480px){.burger-menu-button.false{top:50%;-webkit-transform:translateY(-50%);transform:translateY(-50%)}}@media (min-width: 480px){.burger-menu-button.hidden{display:none}}.burger-menu-button:hover span,.burger-menu-button:focus span{background:#6e220b}.burger-menu-button.clicked span{position:absolute;top:1.6rem;left:.85rem;-webkit-transition:all 0.5s;transition:all 0.5s}.burger-menu-button.clicked .first-line{-webkit-transform:rotate(45deg) scale(1.2);transform:rotate(45deg) scale(1.2)}.burger-menu-button.clicked .middle-line{opacity:0}.burger-menu-button.clicked .last-line{-webkit-transform:rotate(-45deg) scale(1.2);transform:rotate(-45deg) scale(1.2)}
