@-webkit-keyframes hover{from{width:95%;height:95%}to{width:100%;height:100%}}@keyframes hover{from{width:95%;height:95%}to{width:100%;height:100%}}.invitation-body{display:-ms-flexbox;display:flex;-ms-flex-pack:distribute;justify-content:space-around;-ms-flex-align:center;align-items:center;-ms-flex-direction:column;flex-direction:column;position:relative;background:#317c34;width:80%;height:80%;padding:2rem 1.5rem;color:#fefefe;font:2.2rem/1 "SFUIText","sans-serif";border:solid 0.1rem #874206;border-radius:18px}@media (min-width: 768px){.invitation-body{max-width:75%;padding:2.5rem 5rem}}@media (min-width: 900px){.invitation-body{max-width:70%;padding:3rem 6rem}}@media (min-width: 1200px){.invitation-body{max-width:80rem;padding:3.5rem 7rem}}.invitation-body h1{font-size:2.6rem;color:#fefefe;padding:3rem;text-align:center}.invitation-body button{display:block;height:6rem;width:24rem;background-color:#4CAF50;border:none;border-radius:9px;font-size:2rem;color:#fefefe;font-weight:700;margin:1rem auto;-webkit-transition:color .5s;transition:color .5s}.invitation-body button:hover{background-color:#fefefe;color:#2E7D32;cursor:pointer;-webkit-transition:color .5s;transition:color .5s}@media (min-width: 768px){.invitation-body button{margin:1.5rem auto}}@media (min-width: 900px){.invitation-body button{margin:2rem auto}}.invitation-body p{font:1.8rem/2 'Helvetica', 'sans-serif';text-align:center;margin:1rem auto}.invitation-body p:nth-child(2){text-align:justify}.invitation-body .game-description{width:100%;margin:0 auto}
