@-webkit-keyframes hover{from{width:95%;height:95%}to{width:100%;height:100%}}@keyframes hover{from{width:95%;height:95%}to{width:100%;height:100%}}.app-wrapper{display:-ms-flexbox;display:flex;-ms-flex-pack:justify;justify-content:space-between;-ms-flex-align:center;align-items:center;-ms-flex-direction:column;flex-direction:column;background:radial-gradient(#80DEEA, #d8f5f8);min-height:100vh;min-width:350px}.footer{min-height:5rem}@media (max-width: 760px){.footer{min-height:1rem}}.rr{width:100vh}.rr-enter{opacity:0.01;-webkit-transform:translateX(-100%);transform:translateX(-100%)}.rr-enter.rr-enter-active{opacity:1;-webkit-transform:translateX(0%);transform:translateX(0%);-webkit-transition:all 3s ease-in;transition:all 3s ease-in}.rr-enter-active{opacity:1;-webkit-transform:translateX(0%);transform:translateX(0%);-webkit-transition:all 3s ease-in;transition:all 3s ease-in}.rr-exit{opacity:1;-webkit-transform:translateX(0%);transform:translateX(0%)}.rr-exit-active{opacity:0.01;-webkit-transform:translateX(100%);transform:translateX(100%);-webkit-transition:all 3s ease-out;transition:all 3s ease-out}
