@-webkit-keyframes hover{from{width:95%;height:95%}to{width:100%;height:100%}}@keyframes hover{from{width:95%;height:95%}to{width:100%;height:100%}}.spinner{height:100%;width:100%;display:-ms-flexbox;display:flex;-ms-flex-pack:center;justify-content:center;-ms-flex-align:center;align-items:center;-ms-flex-direction:row;flex-direction:row;background:transparent}.spinner.whole-page{height:calc(100vh - 100px)}.spinner .spinner-circle{position:relative;border-radius:50%;display:-ms-flexbox;display:flex;-ms-flex-pack:center;justify-content:center;-ms-flex-align:center;align-items:center;-webkit-animation:spinner 3s linear infinite;animation:spinner 3s linear infinite;width:50px;height:50px;z-index:1000}.spinner .spinner-circle:before{content:'';width:25px;height:50px;position:absolute;top:0;left:0;background:-webkit-gradient(linear, left top, left bottom, from(#90b0cc), to(#eaeff5));background:linear-gradient(#90b0cc, #eaeff5);border-top-left-radius:25px;border-bottom-left-radius:25px}.spinner .spinner-circle:after{content:'';width:25px;height:50px;position:absolute;top:0;right:0;background:-webkit-gradient(linear, left top, left bottom, from(#eaeff5), to(#90b0cc));background:linear-gradient(#eaeff5, #90b0cc);border-top-right-radius:25px;border-bottom-right-radius:25px}.spinner .spinner-circle-center{display:block;height:38px;width:38px;border-radius:50%;background:green;z-index:1001}
